import React, { Component } from "react"

class HomePage extends Component {
  render() {
    return (
      <div className="homeParagraph">
        <h1> Welcome to ASL Strong! </h1>
        <p>Helping people learn the beautiful 3D Langauge that is American Sign Langauge</p>
        <h2> Coming next! </h2>
        <p> Search by handshape. See a Sign and don't know what word it could be? Search via handshapes! </p>
      </div>
    )
  }
}

export default HomePage



